@mixin menu__items {
  display: flex;
  flex-direction: column;

  margin: $lem / 2 0;

  width: calc(100% - #{$lem + $lem / 4});
  @include text-style(subtitle);
  @include text-style(foreground-text);
  @include box-shadow;

  .ul {
    @include ul(underlined);
    padding: 0 $lem / 2;
    margin: $lem 0;

    .li {
      cursor: pointer;

      &:hover,
      &:active,
      &:focus {
        @include colors(blue-on-white);
      }
    }
  }


  @include menu__items--mobile-and-up;
}
